import{useCallback,useMemo}from'react';import{useIntl}from'react-intl';import{Toast}from'@onekeyhq/components';import{ETranslations}from'@onekeyhq/shared/src/locale';import{useWatchListActions}from"../../../states/jotai/contexts/market";export var useWatchListAction=function(){var intl=useIntl();var actions=useWatchListActions();var removeFormWatchList=useCallback(function(coingeckoId){var item={coingeckoId};actions.current.removeFormWatchList(item);},[actions]);var addIntoWatchList=useCallback(function(coingeckoIds){var ids=Array.isArray(coingeckoIds)?coingeckoIds:[coingeckoIds];actions.current.addIntoWatchList(ids.map(function(id){return{coingeckoId:id};}));Toast.success({title:intl.formatMessage({id:ETranslations.market_added_to_watchlist})});},[actions,intl]);var MoveToTop=useCallback(function(coingeckoId){var item={coingeckoId};actions.current.moveToTop(item);},[actions]);var isInWatchList=useCallback(function(coingeckoId){return actions.current.isInWatchList(coingeckoId);},[actions]);return useMemo(function(){return{removeFormWatchList,addIntoWatchList,MoveToTop,isInWatchList};},[MoveToTop,addIntoWatchList,isInWatchList,removeFormWatchList]);};